/*Geral*/
*{margin: 0; padding: 0;
-webkit-box-sizing: border-box;
-moz-box-sizing: border-box;
box-sizing: border-box;
font-size: 18px;
font-family: "Helvetica Neue", sans-serif;
}

a{text-decoration: none; font-weight: bold;}

.nav {background: #370637;}
.nav a{text-align: center; padding: 10px 0; color:#fff;}

/*Telefone*/
@media screen and (max-width: 768px) {
    .header{display: flex; flex-direction: column;}

    .nav{display: none; flex-direction: column; order:2;}
        .nav a{width: 100%;}
            .nav a:hover{background: #750e75;}

    .buttonMob{order:1; text-align: right; padding: 5px 5% 5px 0;  background-color: #370637;}

    .logo{position: absolute; top: 0; left: 5%;}

    .address{order: 3; padding: 10px 5%; line-height: 35px; text-align: center; background: #960f96; color: white;}
        .address img{vertical-align: middle;}
        .address strong{color: white; font-size: 24px;} 
            
    .image-gallery{float:left; width: 100%; margin: 0;}

    .information{float:left; width: 100%;}
    .information div:nth-child(1){flex-grow: 1; text-align: justify; background-color: #370637; color: white; font-weight: bold;}
            .information li{
                list-style: none;
                line-height: 35px;
                padding-left: 20px;
                margin-left: 10px;
            }

    .mapsSitio{width: 100%; margin: 35px 0;}
    .mapsSitio iframe{width: 100%; height: 300px; border: none;}

    .footer{
        display: flex;
        flex-direction: column;
        text-align: center;
        width: 100%;
        background: #370637;
        padding: 15px 5%;
        font-weight: bold;
        line-height: 35px;
    }
        .footer div:nth-child(2){color:#fff; text-align: left;}
}

/*PC*/
@media screen and (min-width: 769px) {
    .header{float: left; width: 100%; box-shadow: 0 2px 10px #370637; background-color: #460a46;}

    .nav{display: flex;}
        .nav a{width: 25%;}
            .nav a:hover{background: #750e75;}

        .buttonMob{display: none;}

        .logo{float: left; width: 30%; padding: 15px 0 15px 10%;}

        .address{float:left; width: 70%; padding: 20px 5% 10px 20%; line-height: 35px; text-align: center;}
            .address img{vertical-align: middle;}
            .address strong{color: #7ad06d; font-size: 24px;}

        .image-gallery{float:left; width: 100%; margin: 0;}

        .information{float:left; width: 100%; margin-bottom: 5%;}
            .information div:nth-child(1){flex-grow: 1; text-align: justify; margin-bottom: 5%; background-color: #370637; color: white; font-weight: bold;}
            .information div:nth-child(2){flex-grow: 5; width: 50%; float: right;}
            .information div:nth-child(3){flex-grow: 5; width: 50%; }
            .information li{
                list-style: none;
                line-height: 35px;
                padding-left: 20px;
                margin-left: 10px;
            }
        .mapsSitio{width: 100%;}
            .mapsSitio iframe{width: 100%; height: 500px; border: none;}

        .footer{
            display: flex;
            width: 100%;
            background: #370637;
            padding: 15px 5%;
            font-weight: bold;
            line-height: 35px;
        }
            .footer div:nth-child(1){flex-grow: 1; text-align: center;}
            .footer div:nth-child(2){flex-grow: 5; color:#fff;}

